.contacto {
    display: grid;
    margin-top: 10px;
    padding: 20px;
    justify-content: center;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 2fr;
    background: rgb(60, 80, 60);
    grid-template-areas:
    "h1con"
    "form"
} 


.contacto h2 {
    display: flex;
    grid-area: h1con;
    margin: 0;
    justify-self: center;
    align-items: center;
    color: white;
}

.contacto label {
    text-align: left;
    color: white;
    margin-bottom: 5px;
}

.contacto .form {
    display: grid;
    grid-area: form;
    justify-self: center;
}

.contacto .form input {
    display: inline-block;
    box-sizing: content-box;
    float: none;
    z-index: auto;
    width: 400px;
    height: auto;
    position: static;
    cursor: default;
    opacity: 1;
    margin: 0 0 10px 0;
    padding: 10px 20px;
    overflow: visible;
    border: 1px solid #b7b7b7;
    border-radius: 3px;
    font: normal 16px/normal;
    color: black;
    text-overflow: clip;
    background: rgba(252,252,252,1);
    box-shadow: 2px 2px 2px 0 rgba(0,0,0,0.2) inset;
    text-shadow: 1px 1px 0 rgba(255,255,255,0.66) ;
    transition: all 200ms cubic-bezier(0.42, 0, 0.58, 1);
    transform: none;
    transform-origin: 50% 50% 0;
}

.contacto .form button {
    display: inline-block;
    box-sizing: content-box;
    float: none;
    z-index: auto;
    width: auto;
    height: auto;
    position: static;
    cursor: pointer;
    opacity: 1;
    margin: 0;
    padding: 10px 20px;
    overflow: visible;
    border-radius: 3px;
    font: normal 16px/normal;
    color: rgba(0, 0, 0, 0.9);
    text-overflow: clip;
    background: #ffffff;
    box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.2) ;
    transition: all 300ms cubic-bezier(0.42, 0, 0.58, 1);
    transform: none;
    transform-origin: 50% 50% 0;
}

